<template>
  <v-form @submit.prevent="login">
    <v-card>
      <v-card-title>
        Log In
      </v-card-title>
      <v-card-text>
        <v-text-field
          v-model="email"
          type="email"
          placeholder="Enter your email"
        />
        <v-text-field
          v-model="password"
          type="password"
          placeholder="Enter your password"
        />
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn type="submit" color="success" class="text-capitalize">
          Log In
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-form>
</template>

<script>
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
export default {
  data() {
    return {
      email: null,
      password: null,
    };
  },
  methods: {
    login() {
      this.$store.dispatch("signInWithEmailAndPassword", {
        email: this.email,
        password: this.password,
      });
    },
  },
};
</script>
